import './App.css';
import Header from "../Header/Header";
import Footer from "../Footer/Footer";
import Main from "../Main/Main";
import back_page1 from '../../assets/BackgroundImage.png';
import back_page2 from '../../assets/BackgroundImage2.png';
import Feature from "../Feature/Feature";
import Sing from "../Sing/Sing";
import {useState} from "react";
import {Redirect, Route} from "react-router-dom";

function App() {

    let [page, setPage] = useState(1)

    let setPageOne = () => {
        setPage(page=1)
    }

    let setPageTwo = () => {
        setPage(page=2)
    }

    let setPageThree = () => {
        setPage(page=3)
    }

  return (
    <div className="app">
        <div className="app__image image-app">
            <img src={back_page1} alt="PHOTO" className="image-app__page1" style={page === 1 ? {display: "block"} : {display: "none"}}/>
            <img src={back_page2} alt="PHOTO" className="image-app__page2" style={page === 3 ? {display: "block"} : {display: "none"}}/>
        </div>
        <Header changePage={{setPageOne, setPageTwo, setPageThree}} state={page}/>
        <div className="content">
            <Redirect from="/" to="/"/>
            <Route exact path="/" component={Main}/>
            <Route path="/feature" component={Feature}/>
            <Route path="/sing" component={Sing}/>
        </div>
        <Footer/>
    </div>
  );
}

export default App;
