import React from 'react';
import './Header.css';
import logo from '../../assets/Logo.png';
import HeaderLink from "./HeaderLink/HeaderLink";

const Header = ({changePage, state}) => {

    let links = [
        {
            "func": changePage.setPageOne,
            "path": "/",
            "title": "Simo"
        },
        {
            "func": changePage.setPageTwo,
            "path": "/feature",
            "title": "Discover"
        },
        {
            "func": "",
            "path": "/",
            "title": "Join"
        },
        {
            "func": changePage.setPageThree,
            "path": "/sing",
            "title": "Sing In"
        }
    ]

    let result
    if(state === 1) {
        result = links
    }else if(state === 2) {
        links.splice(1,1)
        result = links
    }else if(state === 3) {
        links.splice(3,1)
        result = links
    }


    return (
        <header className="header">
            <div className="header__container">
                <div className="header__body">
                    <div className="header__logo">
                        <img src={logo} alt="LOGO"/>
                    </div>
                    <nav className="header__menu menu">
                        <ul className="menu__list">
                            {result.map((link, index) => {
                                    return <HeaderLink key={index} link={link}/>
                            })}
                        </ul>
                    </nav>
                </div>
            </div>
        </header>
    );
};

export default Header;