import React from 'react';
import './Sing.css';
import FormItem from "../FormItem/FormItem";
import MyButton from "../UI/MyButton/MyButton";

const Sing = () => {
    return (
        <section className="sing">
            <div className="sing__container">
                <div className="sing__body">
                    <div className="sing__form form-sing">
                        <div className="form-sing__body">
                            <FormItem title="Name:"/>
                            <FormItem title="Password:"/>
                            <FormItem title="e-mail:"/>
                        </div>
                    </div>
                    <div className="sing__button">
                        <MyButton title="Join Now"/>
                    </div>
                </div>
            </div>
        </section>
    );
};

export default Sing;