import React from 'react';
import './Feature.css';
import MyButton from "../UI/MyButton/MyButton";
import musicTitles from '../../assets/MusicTitles.png';

const Feature = () => {
    return (
        <section className="feature">
            <div className="feature__container">
                <div className="feature__body">
                    <div className="feature__info info-feature">
                        <div className="info-feature__title">
                            <span>Discover new music</span>
                        </div>
                        <div className="info-feature__button button-feature">
                            <div className="button-feature__item"><MyButton title="Charts"/></div>
                            <div className="button-feature__item"><MyButton title="Songs"/></div>
                            <div className="button-feature__item"><MyButton title="Artists"/></div>
                        </div>
                        <div className="info-feature__text">
                            <p>
                                By joing you can benefit by listening to the latest albums released
                            </p>
                        </div>
                    </div>
                    <div className="feature__image">
                        <img src={musicTitles} alt="MUSICTITLES"/>
                    </div>
                </div>
            </div>
        </section>
    );
};

export default Feature;