import React from 'react';
import './Main.css';
import MyButton from "../UI/MyButton/MyButton";

const Main = () => {
    return (
        <section className="main">
            <div className="main__container">
                <div className="main__body">
                    <div className="main__info info-main">
                        <div className="info-main__title">
                            <span>Feel the music</span>
                        </div>
                        <div className="info-main__text">
                            <p>
                                Stream over 10 million songs with one click
                            </p>
                        </div>
                        <MyButton title="Join Now"/>
                    </div>
                </div>
            </div>
        </section>
    );
};

export default Main;